import React, { useRef, useState } from 'react'
import emailjs from '@emailjs/browser';


function RSVP() {

    const [status, setStatus] = useState('');

    const form = useRef();

    const sendEmail = (e) => {
      e.preventDefault();
      console.log();
      emailjs.sendForm('service_8ce1qya', 'template_iw5z45r', form.current, 'user_cdDNS8wNwI2j7N0W8PEOr')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        setStatus('SUCCESS');
        e.target.reset();
    };

    const renderAlert = () => (
      <div className='alertMessage'>
        <p>Votre message a bien été envoyé, merci!</p>
      </div>
    ) 

  return (
    <div
      id="rsvp"
      className='section-padding'
    >
      <div className='container'>
        <div className='row justify-content-center'>
          <div className='col-md-6 bg-white p-40 rounded'>
            {' '}
            <span className='oliven-title-meta text-center'>Participerez-vous?</span>
            <h2 className='oliven-title text-center'>Confirmation s.v.p</h2>
            <h5 className='text-center'><u>Avant le 10 avril</u></h5>
            <br />
            <form 
              className='row'
              ref={form}
              onSubmit={sendEmail}
            >
              <div className='col-md-12'>
                <div className='form-group'>
                  <input
                    type="text"
                    name="name"
                    className='form-control'
                    placeholder="Nom et Prénom"
                    required
                  />{' '}
                </div>
              </div>
              <div className='col-md-12'>
                <div className='form-group'>
                  <input 
                    type="email" 
                    name="user_email"
                    className='form-control' 
                    placeholder="Votre Email" 
                  />{' '}
                </div>
              </div>
              <div className='col-md-12'>
                <div className='form-group'>
                  <textarea
                    name="message"
                    id="message"
                    cols='30'
                    rows='7'
                    className='form-control'
                    placeholder="SVP, veuillez confirmer votre présence en inscrivant les noms qui apparaissent au devant de la lettre d'invitation. Veuillez également préciser votre menu et faire une petite liste de chanson pour le party si vous voulez! Merci et à très bientôt :)"
                  ></textarea>
                </div>
              </div>
              <div className='col-md-12 text-center'>
                <div className='form-group'>
                  <input type="submit" className="btn buttono" value="ENVOYER" />{' '}
                </div>
                {status && renderAlert()}
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}



export default RSVP
