import React from 'react'

function Organization () {
  return (
    <div id='organization' className='organization section-padding bg-pink'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-12 mb-30'>
            <span className='oliven-title-meta'>Mariage</span>
            <h2 className='oliven-title'>Déroulement</h2>
          </div>
        </div>
        <div className='row bord-box bg-img' data-background='images/slider.jpg'>
          <div className='col-md-3 item-box'>
            <h2 className='custom-font numb'>01</h2>
            <h6 className='title'>Ceremonie</h6>
            <p>
              La cérémonie se déroulera au Fairmont, le château Montebello à 18h30, à l'extérieur proche du fleuve. Il y aura des panneaux pour vous indiquer le chemin. En cas de pluie, la cérémonie se fera au Clubhouse.
            </p>
          </div>
          <div className='col-md-3 item-box'>
            <h2 className='custom-font numb'>02</h2>
            <h6 className='title'>Cocktail et Dîner</h6>
            <p>
              Le cocktail et le dîner vous seront offerts après 19h30 au Clubhouse qui est situé à 2 min de l'hôtel.
            </p>
          </div>
          <div className='col-md-3 item-box'>
            <h2 className='custom-font numb'>03</h2>
            <h6 className='title'>Danses/Cérémonie polynésienne</h6>
            <p>
              Lorsque le moment viendra, il y aura des danses et une cérémonie Polynésienne.
            </p>
          </div>
          <div className='col-md-3 item-box'>
            <h2 className='custom-font numb'>04</h2>
            <h6 className='title'>Party</h6>
            <p>
              Après 23h00, ce sera l'heure de faire la fête! On fera les foufous jusqu'à 3h00 du matin, ou plus tard pour les plus courageux!
            </p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Organization
